*{
	font-family: "PT-Sans";
}
h1,
h2,
h3,
h4,
h5,
h6{
	font-family: "OpenSans";
}
p, a{
	font-size: 16px;
	color: #fff;
}
a{
	transition: 0.3s;
	text-decoration: none!important;
	&:hover{
		color: #FFFF00;
	}
	&:active{
		color: #FF0000;
	}
}

header{
	background: url('../images/main-bg.png');
	padding-top: 24px;
	height: 500px;
    background-size: cover;
    img{
    	margin-right: 61px;
    }
	nav{
		ul{
			// display: flex;
			// justify-content: space-between;
			li{
				list-style: none;
				margin-right: 61px;
			}
		}
	}
	.login a:last-child{
		margin-left: 30px; 
	}
	h1{
		text-align: center;
		color: #fff;
		font-weight: 300;
		font-size: 48px;
		margin-top: 90px;
	}
	p{
		text-align: center;
		font-size: 18px;
		margin-top: 84px;
	    padding: 0 110px;
	}
}
main{
	h2{
		font-weight: 700;
		text-align: center;
		color: #000;
		font-size: 18px;
		margin: 80px 0 30px 0;
	}
	.card-head{
		text-align: center;
        height: 129px;
		img{
			max-width: fit-content;
			margin: 0 auto;
		}
	}
	.card-body{
	    padding: 0 35px;
		text-align: center;
		h3{
			font-size: 18px;
			color: #000;
			font-weight: 700;
			font-family: "PT-Sans";
			margin-bottom: 10px;
		}
		p, a{
			color: #000;
			font-size: 14px;
			font-weight: 400;
			text-align: left;
		}
		a{
			color: #1657AB;
			position: absolute;
		    left: 0;
		    right: 0;
		    text-align: center;
		    bottom: 30px;
		}
	}
	.more{
		.card{
			height: 320px;
			background: #FAFAFA;
			border: none;
			transition: 1s;
			&:hover{
				box-shadow: 0px 4px 4px 0px #0000005e;
			}
		}
	}
	.products{
		.col-md-12 p{
			color: #000;
			margin-bottom: 55px;
		}
		.card-product{
			text-align: center;
			img{
				display: block;
				margin: 0 auto;
			}
			h3{
				margin-top: 25px;
			}
		}
	}
}